<script setup>
import top_bar from "@/views/navbar/top_bar.vue";
import bottom_bar from "@/views/navbar/bottom_bar.vue";
import side_bar from "@/views/navbar/side_bar.vue";
import { ref, reactive, computed, onMounted, provide } from "vue";
import { useRouter } from "vue-router";
import { useStore } from "vuex";

const store = useStore();
const router = useRouter();

onMounted(() => {
  save_login();
});

const save_login = () => {
  var user = [];
  user = JSON.parse(window.sessionStorage.getItem("UserRapSarb"));
  if (typeof user != "undefined" && user != null) {
    store.commit("change_eid", user["0"].employee_id);
    store.commit("change_name", user["0"].thai_fullname);
    store.commit("change_email", user["0"].email);
    store.commit("change_company", user["0"].company);
    store.commit("change_title", user["0"].position);

    var redi_url = window.sessionStorage.getItem("redi_url");
    if (typeof redi_url != "undefined" && redi_url != null) {
      setTimeout(() => {
        console.log("Router.go(" + redi_url + ")");
        window.sessionStorage.removeItem("redi_url");
        router.push({ path: redi_url }).catch(() => { });
      }, 500);
    } else {
    }
    // console.log(redi_url);
  } else {
    if (store.getters.get_eid == "") {
      setTimeout(() => {
        console.log("Re Login!");
        save_login();
      }, 500);
    }
  }
};

const alert_toasts = (head, body, color) => {
  store.commit("change_head_toast", head);
  store.commit("change_body_toast", body);
  store.commit("change_bgcolor_toast", color);
  const toastLiveExample = document.getElementById("toast");
  const toastBootstrap = bootstrap.Toast.getOrCreateInstance(toastLiveExample);
  toastBootstrap.show();
};
const moment_format = (val1, val2) => {
  var text = moment(val1).format(val2);
  return text;
};
const enable_tooltips = () => {
  const tooltipTriggerList = document.querySelectorAll(
    '[data-bs-toggle="tooltip"]'
  );
  const tooltipList = [...tooltipTriggerList].map(
    (tooltipTriggerEl) => new bootstrap.Tooltip(tooltipTriggerEl)
  );
};
const set_reload = () => {
  setTimeout(() => {
    window.location.reload(true);
  }, 300);
};
const go_to_route_name = (value) => {
  router.push({ name: value });
};
const go_to_route_path = (value1, value2) => {
  router.push({ path: "/" + value1 + "/" + value2 });
};
const Hash_MD5 = (value) => {
  return MD5(value);
};
const copyURL = (value) => {
  navigator.clipboard.writeText(value);
  alert_toasts("Copy", value, "bg-info");
};

provide("m_format", moment_format);
provide("toast", alert_toasts);
provide("e_tooltips", enable_tooltips);
provide("s_reload", set_reload);
provide("gtr_name", go_to_route_name);
provide("gtr_path", go_to_route_path);
provide("Hash_MD5", Hash_MD5);
provide("copy_text", copyURL);
</script>

