import {
  createRouter,
  createWebHistory,
  createWebHashHistory,
} from "vue-router";
const initial = "RapSarb";
const routes = [
  {
    path: "/",
    name: "home_home",
    component: () => import("../views/home/home.vue"),
    meta: { title: initial + " Home", requiresAuth: true },
  },
  {
    path: "/qr_main",
    name: "qr_main",
    component: () => import("../views/qr/main.vue"),
    meta: { title: initial + " Create", requiresAuth: true },
  },
  {
    path: "/qr_create_topic",
    name: "qr_create_topic",
    component: () => import("../views/qr/create_topic.vue"),
    meta: { title: initial + " สร้างหัวข้อ", requiresAuth: true },
  },
  {
    path: "/qr_list_topic/:prev?",
    name: "qr_list_topic",
    component: () => import("../views/qr/list_topic.vue"),
    meta: { title: initial + " รายการรับทราบ", requiresAuth: true },
  },
  {
    path: "/reg_main",
    name: "reg_main",
    component: () => import("../views/reg/main.vue"),
    meta: { title: initial + " Register", requiresAuth: true },
  },
  {
    path: "/reg_read_rqcode",
    name: "reg_read_rqcode",
    component: () => import("../views/reg/read_rqcode.vue"),
    meta: { title: initial + " แสกน", requiresAuth: true },
  },
  {
    path: "/reg_register/:order?",
    name: "reg_register",
    component: () => import("../views/reg/register.vue"),
    meta: { title: initial + " ลงทะเบียน", requiresAuth: true },
  },
  {
    path: "/reg_list_register",
    name: "reg_list_register",
    component: () => import("../views/reg/list_register.vue"),
    meta: { title: initial + " รายการลงทะเบียน", requiresAuth: true },
  },
  {
    path: "/report_main",
    name: "report_main",
    component: () => import("../views/report/main.vue"),
    meta: { title: initial + " Report", requiresAuth: true },
  },
  {
    path: "/report_summary_register",
    name: "report_summary_register",
    component: () => import("../views/report/summary_register.vue"),
    meta: { title: initial + " รายงานลงทะเบียน", requiresAuth: true },
  },
  {
    path: "/report_detail_register",
    name: "report_detail_register",
    component: () => import("../views/report/detail_register.vue"),
    meta: { title: initial + " รายละเอียดการลงทะเบียน", requiresAuth: true },
  },
  {
    path: "/preview_order",
    name: "preview_order",
    component: () => import("../components/preview/preview_order.vue"),
    meta: { title: initial + " รายละเอียดรับทราบ", requiresAuth: true },
  },
  {
    path: "/contact_main",
    name: "contact_main",
    component: () => import("../views/contact/main.vue"),
    meta: { title: initial + " ติดต่อ", requiresAuth: true },
  },
];

const router = createRouter({
  history: createWebHashHistory(process.env.BASE_URL),
  routes,
});

router.beforeEach((to, from, next) => {
  const title = to.meta.title;
  if (title) document.title = title;
  var path_url = document.URL.split("/");
  var cut_url = document.URL.split("/#/");
  var check_url = cut_url[1].split("/");
  //ตัดจาก / url ตัวอื่นๆ เก็บค่า แล้วค่อยไป login
  if (check_url.length > 1) {
    if (check_url[1] != "" && typeof check_url[1] != "undefined") {
      window.sessionStorage.setItem(
        "redi_url",
        "/" + check_url[0] + "/" + check_url[1]
      );
    } else {
      window.sessionStorage.setItem("redi_url", "/" + check_url[0]);
    }
  } else {
  }
  var authUser =
    '[{"thai_fullname":"มานี มีนา","section":"Programmer","employee_id":"A0001","company":"AAA","email":"abc@def.xxx"}]';
  sessionStorage.setItem("UserRapSarb", authUser);
  next();
});

export default router;
